import React from 'react';
import { ArrayView } from './ArrayView';
import { MatchView } from './MatchView';
import { ObjectView } from './ObjectView';
import { ScalarView } from './ScalarView';
import { SchemaViewProps } from './types';

export function SchemaView(props: SchemaViewProps) {
  const { schema } = props;
  if (schema.anyOf) {
    return (
      <MatchView
        {...props}
        schema={schema.anyOf}
        label="Any of the following"
      />
    );
  }
  if (schema.oneOf) {
    return (
      <MatchView
        {...props}
        schema={schema.oneOf}
        label="One of the following"
      />
    );
  }
  if (schema.allOf) {
    return (
      <MatchView
        {...props}
        schema={schema.allOf}
        label="All of the following"
      />
    );
  }
  switch (schema.type) {
    case 'array':
      return <ArrayView {...props} />;
    case 'object':
    case undefined:
      return <ObjectView {...props} />;
    default:
      return <ScalarView {...props} />;
  }
}
